import { ReactiveVariable } from "vue/macros";
import { FormFieldProps } from "@/views/components/form/types";
import { reactive } from "vue";
import { requiredRules } from "@/kernel/validation/vuelidate/requiredRules";

export type RoleFormFields = ReactiveVariable<{
  name: FormFieldProps;
}>;

export const useFields = () => {
  const fields: RoleFormFields = reactive({
    name: {
      value: "",
      id: "role",
      label: "Наименование",
      rules: requiredRules(),
    },
  });

  return {
    fields,
  };
};
