import { computed } from "vue";
import { BREAKPOINTS } from "@/kernel/constants/Breakpoints";
import { storeToRefs } from "pinia";
import { useStore } from "@/store";

export const useFieldVariant = () => {
  const { windowWidth } = storeToRefs(useStore());
  const fieldVariant = computed(() => {
    return windowWidth.value >= BREAKPOINTS.TABLET ? "horizontal" : "vertical";
  });
  return {
    fieldVariant,
  };
};
