import { ref } from "vue";
import { useFormValidate } from "@/use/useFormValidate";
import { CrudFormCreateInterface } from "@/views/modules/CrudForms/CrudFormInterface";
import { useSubmitTemplate } from "@/views/modules/CrudForms/use/useSubmitTemplate";
import { useInitTemplate } from "@/views/modules/CrudForms/use/useInitTemplate";
import {
  RoleFormFields,
  useFields,
} from "@/views/modules/CrudForms/LacRoleForm/use/useFields";
import { usePrepareData } from "@/views/modules/CrudForms/LacRoleForm/use/usePrepareData";
import { postAgentRoleEdit } from "@/kernel/api/methods/AgentRole";

export const useRoleEditForm: (
  role: string
) => CrudFormCreateInterface<RoleFormFields> = (role) => {
  const { setFieldRef, validateForm, resetValidations } = useFormValidate();
  const isInit = ref(false);

  const { fields } = useFields();
  const { prepareData } = usePrepareData(fields);
  const { submitTemplate } = useSubmitTemplate(validateForm);
  const { initTemplate } = useInitTemplate();

  async function init() {
    fields.name.value = role;
    isInit.value = true;
  }

  async function submit() {
    const payload = prepareData();
    const res = await postAgentRoleEdit(role, payload.role);

    if (res.error) {
      throw new Error(
        res.data.info
          ? JSON.stringify(res.data.info, null, 2)
          : "Не удалось изменить роль"
      );
    }
  }

  return {
    setFieldRef,
    validateForm,
    resetValidations,
    fields,
    init: initTemplate(init),
    submit: submitTemplate(submit),
    isInit,
  };
};
