import { RoleAddData } from "@/kernel/api/dto/AgentRole";
import { RoleFormFields } from "@/views/modules/CrudForms/LacRoleForm/use/useFields";

export const usePrepareData = (fields: RoleFormFields) => {
  function prepareData(): RoleAddData {
    const res: RoleAddData = {
      role: fields.name.value,
    };
    return res;
  }

  return { prepareData };
};
