import { useStoreModal } from "@/store/modal";
import { AvailableModalsEnum } from "@/store/modal/types";
import { useRouter } from "vue-router";

export const useSubmitTemplate = (validateForm: () => Promise<boolean>) => {
  const modals = useStoreModal();
  const router = useRouter();

  function submitTemplate(
    callback: () => Promise<void>,
    hideSuccess?: boolean
  ) {
    return async () => {
      if (await validateForm()) {
        try {
          modals.open(AvailableModalsEnum.LOADER);
          await callback();
          !hideSuccess &&
            modals.open(AvailableModalsEnum.SUCCESS, {
              callback: () => router.back(),
            });
        } catch (e) {
          modals.open(AvailableModalsEnum.ERROR, {
            text: (e as ErrorEvent).message,
          });
        }
      }
    };
  }

  return { submitTemplate };
};
